@import url("https://www.w3schools.com/w3css/4/w3.css");

#dialog-section {
  position: relative;
}

#add-dialog,
#edit-dialog,
#delete-dialog {
  display: block;
}

#delete-content {
  margin-bottom: 10px;
}

.w3-container
{ 
    background-color: white;
    margin: auto;
    color: #7C1A1C; 
}

#close-button
{ 
  position:top;
  float: right;
  color: white;
  padding: 5px 5px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background-color: black;
}




#img-prev {
  max-width: 100%;
}

#delete-dialog button {
  margin-right: 10px;
}

@media only screen and (min-width: 600px) {
  #img-prev-section {
    flex: 1;
  }

  #img-prev {
    max-width: 100%;
  }

  #img-upload {
    flex: 6;
  }
}