#OtherProducts, #Pallets, #Trucking, #pricing-delivery
    {
    background:white; 
    padding: 10px; 
    margin: 20px auto; 
    width:70%; 
    border:5px solid var(--primary-color);
    box-shadow: 5px 5px;
    font-size: 20px;
    text-align: center;
    }

#pricing-delivery
{ 
    margin-bottom:0;
}
ul

    { 
    list-style-type: none;
    text-align: center;
    }
    li 
    {
        line-height: 1.8; 
    }
    #product-pic
    { 
       max-width: 90%;
    }

h1
{ 
    font-family: Arial, Helvetica, sans-serif;
    font-size: 30px;
}

#products-page
{ 
    background-color: #7C1A1C;
}

#page-heading
{ 
    text-align: center;
    font-size:35px;
    font-weight: bold; 
    color:white; 
}

@media only screen and (min-width:800px){
    #OtherProducts, #Pallets, #Trucking, #pricing-delivery
    { 
        margin:20px;
        display:flexbox;
    }

    #products-page
    { 
        align-items: center;
        margin: auto; 
        padding: auto;
    }
    #product-pic
    { 
        max-width: 400px;
        max-height: 400px;
    
    }
}